<template>
    <div class="container">
        <form>
          <div class = "field">
            <label class = "label">Name</label>
            <input
              class = "input"
              type="text"
              v-model="name"
              name="name"
              placeholder="Your Name"
            >
          </div>

          <div class = "field">
            <label class = "label">Email</label>
            <div class = "control has-icons-left">
              <input
                class = "input"
                type="email"
                v-model="email"
                name="email"
                placeholder="Your Email"
                >
              <span class = "icon is-small is-left">
                <font-awesome-icon icon="envelope" />
              </span>
            </div>
          </div>

        <div class = "field">
          <div class = "control">
            <button class = "button has-text-primary-dark" onclick = "sendEmail()"><strong>Submit</strong></button>
          </div>
        </div>

        </form>
    </div>
</template>

<script>
import emailjs from 'emailjs-com';

export default {
  name: 'ContactUs',
  data() {
    return {
      name: '',
      email: ''
    }
  },
  methods: {
    sendEmail(e) {
      try {
        emailjs.sendForm('service_bvp3ltp', 'template_ebo9f3a', e.target,
        '6BJf4qA5YFwnY7_qz', {
          name: this.name,
          email: this.email
        })

      } catch(error) {
          console.log({error})
      }
      // Reset form field
      this.name = ''
      this.email = ''
    },
  }
}
</script>
